import React, { Component } from 'react';
import { Container, Row, Col, Card , Modal} from 'react-bootstrap';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Paper,Button} from '@material-ui/core';
import { Link } from 'react-router-dom';
import Pagination from '../pagination/pagination.component';
import PlayCircleFilledIcon from '@material-ui/icons/PlayCircleFilled';
import CardBiography from '../card-biography/card-biography.component';

export default class YourBiographiesPage extends Component {

    filterdArray = [];
    constructor(props){
        super(props);
        this.state = {
            data : this.props.data 
        }
        this.state.data.map(bio=>{
            if(bio.questions[0] && bio.questions[0].video){
                this.filterdArray.push(bio);
            }
        });
    }
    componentDidMount(){
        this.setState({...this.state , filterdArray : this.filterdArray});
    }
    render() {
        console.log(this.state.filterdArray)
        if (!this.state.filterdArray) {
            return (<div>Loading...</div>)
        }
        else {
            // const handleClose = () => this.setState({...this.state , show : false});
            // const handleShow = () => this.setState({...this.state , show : true});
            return (
                <div>
                    <Container className="container-responsive">
                        <Paper elevation={3}>
                            <Row elevation={3} className="bg-light m-0 p-3" style={{ borderBottom: '1px solid #f2f2f2' }}>
                                <Col xs={6}>
                                    <h5 className="mb-0">Your Biographies</h5>
                                </Col>
                                <Col className="text-right" xs={6}>
                                    <h6 className="bold mb-0"
                                    >Sort by: <Link className="text-orange" to='' 
                                    onClick={e=>{
                                        e.preventDefault();
                                        this.setState({...this.state , filterdArray : this.state.filterdArray.reverse()});
                                        }
                                    }>Latest</Link> <ExpandMoreIcon /></h6>
                                </Col>
                            </Row>
                            <Pagination card={this.filterdArray}>
                                <CardBiography />
                            </Pagination>
                        </Paper>
                    </Container>
                </div>);
        }
    }
}