import React, { Component } from 'react';
import {  Container , Row , Col , Card } from 'react-bootstrap';
import { withRouter } from 'react-router';
import { graphql  } from 'react-apollo';
import ApolloClient from 'apollo-boost';
import { ApolloProvider } from '@apollo/react-hooks';
import * as compose from 'lodash.flowright';
import { gql } from 'apollo-boost';
import { Button } from '@material-ui/core';
import Header from '../header/header.component';
import Footer from '../footer/footer.component';
import YourBiographiesPage from '../your-biographies-page/your-biographies-page';
import { Link } from 'react-router-dom'; 
import Loader from 'react-loader-spinner';
import { createApolloFetch }  from 'apollo-fetch';

import img from '../../images/john.png';

export default class  Biographies extends Component{
    constructor(props){
        super(props);
        this.state = {
        }
    }
    // componentDidUpdate(prevProps) {
    //     console.log(prevProps);
    //     if (prevProps !== this.props) {
    //         if(this.props.get_biographies){
    //             this.setState({...this.state , biographies : this.props.get_biographies.biosYours.data});
    //             console.log(this.state);
    //         }
    //     }

    // }
    componentDidMount(){
        let that = this;
        const token = JSON.parse(localStorage.getItem('jwt')).BearerTokenResponse.access_token;

        const uri = 'https://graphql.spreago.com/graphql';

        const apolloFetch = createApolloFetch({ uri });

        apolloFetch.use(({ request, options }, next) => {
        if (!options.headers) {
            options.headers = {};  // Create the headers object if needed.
        }
        options.headers['authorization'] = `Bearer ${token}`;

        next();
        });
        apolloFetch({
        query: 
        `query{
            biosYours(first: 500 ){
              data{
                id
                user{
                  name
                }
                title
                speciality{
                    name_en
                  }
                created_at
                questions{
                  video{
                    duration
                    created_at
                    url
                    frame{
                      url
                    }
                  }
                }
                
              }
              __typename
            }
          }
        `,
        }).then(res => {
            console.log(res.data.biosYours.data);
            if(res.data.biosYours.data){
                that.setState({...that.state , biographies : res.data.biosYours.data});
            }
        });

         console.log(this.props)
        // if (prevProps !== this.props) {
        //     if(this.props.get_biographies.biosYours){
        //         console.log(this.props.get_biographies.biosYours.data);
        //         this.setState({...this.state , biographies : this.props.get_biographies.biosYours.data});
        //     }
        // }
    }
    render(){
            const  biographies = this.state.biographies;
            if(!biographies){
                return(
                    <div className="loading-custom">
                    <Loader
                        type="Oval"
                        color="#E1631C"
                        height={100}
                        width={100}
                        />
                    </div>
                )
            }
            else{
        return(
                <div>
                    <Header/>
                    <Container  className="pt-5 container-fluid-responsive">
                        <Row>
                            <Col sm="3" className="p-0 pt-2">
                                <Container className="container-responsive">
                                    <Card className="bg-white shadow pt-3 pb-5 border-0">
                                        <div className="text-center pb-3">
                                                <img src={img} alt="interview img" width="120px" className="rounded-circle" />  
                                            </div>
                                            <div className="text-center">
                                                <div className=""><h6>{biographies[0].user.name}</h6></div>
                                                <div><p className="text-dark">0 Follower <span> . 0 Biography </span></p></div>
                                                <Button variant="contained" className="btn-orange text-white pb-1 pt-1 small-text" color="primary" size="large"><Link className="text-white" to='/createBiography'>Create Biography</Link></Button>
                                            </div>
                                        </Card>
                                </Container>
                            </Col>
                            <Col sm={9} className="p-0 pt-2">
                                <YourBiographiesPage data={biographies}/>
                            </Col>
                        </Row>
                    </Container>
                <Footer/>
            </div>  
        );
    }
}
}

