import React, { Component } from 'react';
import { withRouter } from 'react-router';
import { graphql } from 'react-apollo';
import * as compose from 'lodash.flowright';
import { gql } from 'apollo-boost';
import { Container, Row, Col } from 'react-bootstrap';
import {
    TextField,
    InputLabel, Button, Chip, Typography
} from '@material-ui/core';
import { Autocomplete } from '@material-ui/lab';

import './overview.style.css';

class Overview extends Component {
    constructor(props) {
        super(props)
        console.log(JSON.parse(localStorage.getItem('jwt')).BearerTokenResponse.access_token);
        this.state = {
            privacy: 1,
            public : 1,
            private : 2,
            brief : "",
            speciality: "",
            tags: [],
            specialities: [],
            tagsChoosen: ""
        }
    }
    componentDidMount(props){
        console.log(this.props);
    }
    componentDidUpdate(prevProps) {
        if (prevProps !== this.props) {
            console.log(this.props.tags);
            //    if(this.props.speciality.specialities && this.props.tags.tag)
            if (this.props.speciality.specialities && this.props.get_privacy.privacies && this.props.tags.tag)
                // this.setState({...this.state , specialities : this.props.speciality.specialities , tags : this.props.tags.tag});
                this.setState({ ...this.state, specialities: this.props.speciality.specialities ,  tags : this.props.tags.tag , public : this.props.get_privacy.privacies[0] , private : this.props.get_privacy.privacies[1] });
        }
        console.log(prevProps);
    }
    render() {
        const handleChange = prop => event => {
            this.setState({ ...this.state, [prop]: event.target.value });
        };
        const checkTitle = (title) => {
            if (title.length < 3 || title.length > 32) {
                this.setState({ ...this.state, errorMessage: 'This field must be between 3 and 191 characters long.' });
                return false;

            } else {
                this.setState({ ...this.state, errorMessage: '' });
                return true;
            }
        }
        const checkBrief = (brief) => {
            if (brief.length > 5000) {
                this.setState({ ...this.state, briefErrorMessage: 'This field must be max  5000 characters long.' });
                return false;

            } else {
                this.setState({ ...this.state, briefErrorMessage: '' });
                return true;
            }
        }
        const checkSpeciality = () =>{
            if(!this.state.specialityId){
                this.setState({ ...this.state, specialityErrorMessage: 'The data you entered is invalid.' });
                return false;
            }else{
                this.setState({ ...this.state, specialityErrorMessage: '' });
                return true;
            }
        }       
        if( this.state.specialities.length < 1 && this.state.tags.length < 1 && this.state.public.length < 1 && this.state.private.length < 1){
            console.log("loading!");
            return (
                <div><h5 className="text-dark">Loading....</h5></div>
            ) 
         }else{
        return (
            <div className="pb-3">
                <form
                    onSubmit={e => {
                        e.preventDefault();
                        if (checkTitle(this.state.title) && checkSpeciality() && checkBrief(this.state.brief)) {
                            console.log(this.state)
                            this.props.create_bio({
                                variables: {
                                    privacies_id: this.state.privacy,
                                    specialities_id: this.state.specialityId,
                                    title: this.state.title,
                                    brif: this.state.brief,
                                    tags: []
                                }
                            }).then((res) => {
                                console.log(res.data.bioCreate.bio.id);
                                if (res.data.bioCreate.Status.code === 200) {
                                    // go to next form  
                                    let list = document.getElementById("left-tabs-example");
                                    let nav_items = list.querySelectorAll(".nav-item a");
                                    nav_items[0].setAttribute("data-id",res.data.bioCreate.bio.id);
                                    nav_items[1].setAttribute("aria-disabled","false")
                                    nav_items[1].classList.remove("disabled")
                                    nav_items[1].click();
                                }
                            }).catch((err)=>this.setState({...this.state, ConnectionErorr : "Check your internet connection" }));
                        }
                    }}
                >
                    <Container className="overview shadow border-rounded">
                        <Row>
                            <Col className="border-bottom bg-light p-5" sm={12}>
                                <div className="title">
                                    <h4 className="text-dark">Overview</h4>
                                    <p className="text-grey mb-0">Provide the employer with your main information</p>
                                </div>
                            </Col>
                            <Col sm={12} className="bg-white p-5">
                                <InputLabel htmlFor="title">
                                    Title<span className="text-danger"> *</span>
                                </InputLabel>
                                <TextField
                                    required
                                    id="title"
                                    name="title"
                                    value={this.state.title}
                                    onChange={handleChange('title')}
                                    className="pt-1 pb-1"
                                    placeholder="Write your title"
                                    fullWidth
                                    variant="outlined"
                                />
                                <Col xs={12} className="p-0  pb-4">
                                    <Typography variant="subtitle1" className="text-danger">{this.state.errorMessage}</Typography>
                                </Col>
                                <InputLabel htmlFor="brief">
                                    Brief
                                </InputLabel>
                                <TextField
                                    name="brief"
                                    id="brief"
                                    value={this.state.brief}
                                    onChange={handleChange('brief')}
                                    className="pt-1 pb-1"
                                    multiline
                                    rows="7"
                                    fullWidth
                                    variant="outlined"
                                    placeholder="Write your Brief"
                                    helperText="0 / 5000 Characters (Minimum 50)"
                                />
                                <Col xs={12} className="p-0 pb-4">
                                    <Typography variant="subtitle1" className="text-danger">{this.state.briefErrorMessage}</Typography>
                                </Col>
                                <InputLabel htmlFor="speciality">
                                    Speciality<span className="text-danger"> *</span>
                                </InputLabel>
                                <Autocomplete
                                    fullWidth
                                    name='speciality'
                                    onChange={(event, value) => {
                                        this.setState({ ...this.state, speciality: value });
                                        let specialitychoosen = [];
                                        console.log(value);
                                        specialitychoosen = this.state.specialities.filter((speciality)=>{
                                            return speciality.name === value
                                        });
                                        console.log(specialitychoosen);
                                        if(specialitychoosen.length > 0){
                                            this.setState({...this.state ,specialityErrorMessage:"",specialityId : specialitychoosen[0].id})
                                        }
                                        else
                                        {
                                            this.setState({...this.state , specialityId : ''})
                                        }
                                    }}
                                    id="free-solo-demo"
                                    className="pt-1 pb-1"
                                    freeSolo
                                    options={this.state.specialities.map(option => option.name)}
                                    renderInput={params => (
                                        <TextField {...params} variant="outlined" placeholder="choose speciality" />
                                    )}
                                />
                                <Col xs={12} className="p-0 pb-4">
                                    <Typography variant="subtitle1" className="text-danger">{this.state.specialityErrorMessage}</Typography>
                                </Col>
                                <InputLabel htmlFor="tags">
                                    Related tags
                                </InputLabel>
                                <Autocomplete
                                    multiple
                                    id="tags"
                                    className="pt-1 mb-4"
                                    onChange={
                                        (event, value) => this.setState({ ...this.state, tagsChoosen: value })
                                    }
                                    options={this.state.tags.map(option => option.name)}
                                    freeSolo
                                    renderTags={(value, getTagProps) =>
                                        value.map((option, index) => (
                                            <Chip variant="outlined" label={option} {...getTagProps({ index })} />
                                        ))
                                    }
                                    renderInput={params => (
                                        <TextField {...params} variant="outlined" placeholder="Favorites" />
                                    )}
                                />
                                <InputLabel htmlFor="privacy">
                                    Privacy<span className="text-danger"> *</span>
                                </InputLabel>
                                <div className="pt-1 pb-4">
                                    <div className="input-radio">
                                        <input type="radio" id="public" name="privacy" onChange={handleChange('privacy')} value={this.state.public.id} defaultChecked />
                                        <label htmlFor="public" className="first-btn">Public</label></div>
                                    <input type="radio" id="private" onChange={handleChange('privacy')} name="privacy" value={this.state.private.id} />
                                    <label htmlFor="private" className="last-btn">Private</label>
                                </div>
                            </Col>
                            <Col xs={12} className="p-0 pb-4">
                                <Typography variant="subtitle1" className="text-danger">{this.state.connectionError}</Typography>
                            </Col>
                        </Row>
                    </Container>
                    <div className="pt-4 text-right text-responsive-center">
                        <Button type="submit" size="large" className="btn-orange-gradiant pr-5 pl-5" variant="contained" to="#" >Next</Button>
                    </div>
                </form>
            </div>
        );
    }
    }
}
const GET_SPECIALITY = gql`
query{
    specialities{
      id
      context
      name
      name_en
      name_ar
      created_at
      updated_at
      __typename
    }
  }`
const GET_TAGS = gql
    `query{
    tag{
      id
      name
      __typename
    }
  }`

const CREATE_BIO = gql
    `mutation($privacies_id : Int ! $specialities_id :Int ! 
    $title : Title !  $brif : Text $tags :[Name!] ){
    bioCreate(Inputs:{
      privacies_id : $privacies_id
      specialities_id : $specialities_id 
      title : $title
      brif : $brif
      tags : $tags
    }){
      Status{
        code
        message
      }
      bio{
        id
        user{
              name
              email
            }
      }
      __typename
    }
  }`
  const GET_PRIVACY = gql`
        query{
            privacies{
            id
            context
            name
            name_en
            name_ar
            created_at
            updated_at
            __typename
            }
        }
  `

//export default compose(graphql(GET_SPECIALITY),graphql(GET_CONTRIES_AND_CITIES))(withRouter(Overview));
export default compose(
    graphql(GET_SPECIALITY, {
        name: "speciality"
    }),
    graphql(GET_TAGS, {
        name: "tags"
    }),
    graphql(CREATE_BIO, {
        name: "create_bio"
    }),
    graphql(GET_PRIVACY ,{
        name : "get_privacy"
    })
)(withRouter(Overview));
