import React, { Component } from 'react';
import { withRouter } from 'react-router';
import { graphql } from 'react-apollo';
import * as compose from 'lodash.flowright';
import { gql } from 'apollo-boost';
import { Container, Row, Col , Modal} from 'react-bootstrap';
import { InputLabel, IconButton, Button, InputBase , Typography } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';

import './question.style.css';
import more from './images/more.png';

class Questions extends Component {
    constructor() {
        super();
        this.state = {
            range: "250",
            questionInputs : [],
            questions: [],
            inputs: [],
            yourQuetions : "",
            show : false,

        }
        console.log(this.state);
    }
    componentDidMount() {
        // n
        let add = document.querySelector(".custom-input button");
        add.addEventListener("click", function () {
            
            if (this.state.title.length < 3 || this.state.title.length > 32) {
                this.setState({ ...this.state, errorMessage: 'This field must be between 3 and 191 characters long.' });
                return false;
            }  
            else {
                this.setState({ ...this.state, errorMessage: '' });
                // make a list 
                let inputValue = document.querySelector(".custom-input input").value;
                let questionInputs = { title: inputValue, maximum_time: this.state.range, minimum_time: 0, mandatory: true, ordering: 0 }
                this.setState({ ...this.state, questions :[...this.state.questions , questionInputs] ,title : "" , range : "250" , yourQuetions : "Your Questions"});
                console.log(this.state);
                // show edit menu
                // let btns = document.querySelectorAll(".more-btn");
                // for (let i = 0; i < btns.length; i++) {
                //     btns[i].addEventListener("click", function () {
                //         this.querySelector(".custom-list").classList.toggle("d-none");
                //     });
                // }
                // let edit = document.querySelectorAll(".delete-option");
                // for (let i = 0; i < edit.length; i++) {
                //     edit[i].addEventListener("click",function(){
                //     this.parentElement.parentElement.parentElement.parentElement.remove();
                // })
                //     }
                return true;
            }
        }.bind(this));
    }
    
    handelBackBtn =()=> {
        let list = document.getElementById("left-tabs-example");
        let nav_items = list.querySelectorAll(".nav-item a");
        nav_items[0].click(); 
    }
    render() {
        const handleChange = prop => event => {
            this.setState({ ...this.state, [prop]: event.target.value });
        };
        const checkquestion = () => {
            console.log(this.state.questions.length);
            if (this.state.questions.length < 1) {
                this.setState({ ...this.state, errorMessage: 'You must enter at least one question' });
                return false;

            } else {
                this.setState({ ...this.state, errorMessage: '' });
                return true;
            }
        }

        const handleClose = () => this.setState({...this.state , show : false});
        const handleShow = () => this.setState({...this.state , show : true});
        return (
            <div className="pb-3">
                <form onSubmit={e=>{
                    e.preventDefault();
                    // get biography id 
                    let list = document.getElementById("left-tabs-example");
                    let nav_items = list.querySelectorAll(".nav-item a");
                    let bioId = nav_items[0].getAttribute("data-id");

                    // get list of questions 
                    const listItems = document.querySelectorAll('.question-title');
                    const listLimit = document.querySelectorAll('.question-limit');
                    let inputs = "" , arrayOfQuestions = [];;
                    for (let i = 0; i < listItems.length; i++) {
                        let questionInputs = { title: listItems[i].textContent, maximum_time: listLimit[i].textContent, minimum_time: 0, mandatory: true, ordering: 0 }
                         inputs =  {question : null,questionInputs : questionInputs}
                         arrayOfQuestions.push(inputs);
                    }
                    console.log(arrayOfQuestions);

                    if(checkquestion()){
                   // send mutation varaibles
                    this.props.createQuestions({
                        variables: {
                                bio_id:{ id : bioId},
                                Inputs: arrayOfQuestions
                        }
                    }).then((res) => {
                        console.log(res.data.questionArray.Status.code)
                        if(res.data.questionArray.Status.code === 200){
                            // go to next form  and make it not dissappled !
                            let list = document.getElementById("left-tabs-example");
                            let nav_items = list.querySelectorAll(".nav-item a");
                            nav_items[2].setAttribute("aria-disabled","false");
                            nav_items[2].classList.remove("disabled");
                            nav_items[2].click();       
                        }
                    }).catch((err)=>this.setState({...this.state, ConnectionErorr : "Check your internet connection" }));
                    }
                }}>
                <Container className="overview shadow border-rounded bg-white">
                    <Row>
                        <Col className=" border-bottom p-5 bg-light" sm={12}>
                            <div className="title">
                                <h4 className="text-dark">Questions</h4>
                                <p className="text-grey mb-0">Add your questions that helps the employer to know you more</p>
                            </div>
                        </Col>
                        <Col className="text-dark p-5">
                                <InputLabel htmlFor="title">
                                    Question Title<span className="text-danger"> *</span>
                                </InputLabel>
                                <div className="custom-input mt-1">
                                    <InputBase
                                        placeholder="Write your question title"
                                        onChange={handleChange('title')}
                                        id="title"
                                        name="title"
                                        value={this.state.title}
                                        className="w-90 pl-2"
                                    />
                                    <IconButton color="primary" className="offset-8" aria-label="directions">
                                        <AddIcon />
                                    </IconButton>
                                </div>
                                <Col xs={12} className="p-0 pb-4">
                                    <Typography variant="subtitle1" className="text-danger">{this.state.errorMessage}</Typography>
                                </Col>
                                <div className="pt-3 pb-3">
                                    <Row>
                                        <Col sm={7} xs={12} className="text-left"> <label for="range">Time limit ( second )*</label></Col>
                                        <Col sm={5} xs={12} className="text-center">
                                            <input type="range" id="range" name="range" min="10" onChange={handleChange('range')} value={this.state.range} max="500" />
                                            <Row className="m-0 ">
                                                <Col><span className="pr-1">10 sec</span></Col>
                                                <Col><span className="pl-3 pt-3">{this.state.range} sec</span></Col>
                                            </Row>
                                        </Col>
                                    </Row>
                                </div>
                             <div><p className="mb-0">{this.state.yourQuetions}<span className="border-b-1"></span></p></div>
                                <div className=" text-dark pt-2 pb-2">
                                    <ol className="w-100 pl-0 custom-ol">
                                        {this.state.questions.map(input => 
                                               <Row className="bg-lightgrey m-0 pt-2 pb-2 mt-1">
                                                    <Col sm={1} xs={1} className="text-center dots p-0">
                                                        <button type="button" className="more-btn">
                                                            <img src={more} alt="more" />
                                                            <ul className="shadow custom-list d-none">
                                                                <li className="list-style-none text-center edit-option">
                                                                <button type="button" className="bg-transparent border-0" onClick={handleShow}>
                                                                    Edit
                                                                </button>
                                                                </li>
                                                                <li className="list-style-none text-center delete-option">Delete</li>
                                                            </ul>
                                                        </button>
                                                    </Col>
                                                    <Col sm={11}>
                                                        <li className="w-100">
                                                            <Col sm={10} xs={9} className="p-0 d-inline-block text-left question-title">{input.title}</Col>
                                                            <Col sm={1} xs={2} className="p-0 d-inline-block"><span className="question-limit">{input.maximum_time}</span> sec</Col>
                                                        </li>
                                                    </Col>
                                                </Row>
                                        )}
                                        <Modal  className="modal-edit row m-0 " show={this.state.show} onHide={handleClose}>
                                            <Col xs={12}>
                                            <Modal.Header closeButton>
                                                <Modal.Title>Edit Question</Modal.Title>
                                            </Modal.Header>
                                            <Modal.Body>
                                            <InputLabel htmlFor="title">
                                                Question Title<span className="text-danger"> *</span>
                                            </InputLabel>
                                            <div className="custom-input mt-1">
                                                <InputBase
                                                    placeholder="Write your question title"
                                                    onChange={handleChange('title')}
                                                    id="title"
                                                    name="title"
                                                    value={this.state.title}
                                                />
                                            </div>
                                            <Col xs={12} className="p-0 pb-4">
                                                <Typography variant="subtitle1" className="text-danger">{this.state.errorMessage}</Typography>
                                            </Col>
                                            <div className="pt-3 pb-3">
                                                <Row>
                                                    <Col sm={7} xs={12} className="text-left"> <label for="range">Time limit ( second )*</label></Col>
                                                    <Col sm={5} xs={12} className="text-center">
                                                        <input type="range" id="range" name="range" min="0" onChange={handleChange('range')} value={this.state.range} max="600" />
                                                        <Row className="m-0 ">
                                                            <Col><span className="pr-1">10 sec</span></Col>
                                                            <Col><span className="pl-3 pt-3">{this.state.range} sec</span></Col>
                                                        </Row>
                                                    </Col>
                                                </Row>
                                            </div>
                                            </Modal.Body>
                                            <Modal.Footer>
                                                <Button type="button" size="small" className="btn-orange-gradiant" variant="contained" onClick={handleClose}>
                                                    Save Changes
                                                </Button>
                                            </Modal.Footer>
                                            </Col>
                                        </Modal>
                                    </ol>
                                </div>
                        </Col>
                    </Row>
                </Container>
                <div className="w-100">
                    <Col xs="6" className="d-inline-block p-0 pt-4 text-left">
                        <Button type="button" onClick={this.handelBackBtn} size="large" className="btn-white-outline pr-5 pl-5" variant="contained" >Black</Button>
                    </Col>
                    <Col xs="6" className="d-inline-block p-0 pt-4 text-right">
                        <Button type="submit" size="large" className="btn-orange-gradiant question-next pr-5 pl-5" variant="contained">Next</Button>
                    </Col>
                </div>
                </form>
            </div>
        );
    }
}
const CREATE_QUESTIONS = gql`
mutation(
    $bio_id : bioMethodInputs
    $Inputs : [questionEdit!]
) {
    questionArray (
        Inputs : {
            bio    : $bio_id
            Inputs : $Inputs
        }
    ) {
        Status {
            code
            title
            message
            check
        }
        results {
            code
            title
            message
            check
        }
        bio{
            title
            questions{
                id
                title
                maximum_time
              }
        }
    }
}
`

export default compose(
    graphql(CREATE_QUESTIONS, {
        name: "createQuestions"
    })
)(withRouter(Questions));